
import { defineComponent } from "vue";

export default defineComponent({
  data() {
      return {
          userData: {}
      }
  },
  methods: {
      loadUserData() {
          this.userData = JSON.parse(localStorage.getItem("userData") || '{}');
      },

      routeUser(route: string) {
          this.$router.push({
              path: '/' + route,
          });
      }
  },
  // Calls function on page load
  mounted() {
      this.loadUserData();
  }
});
